@import url(https://fonts.googleapis.com/css?family=Lato|Roboto);
body {
  margin: 0;
  padding: 0;
  overflow: hidden;
  width: 100vw;
  height: 100vh;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

:root {
  --topBar-color: #1AA6B7;
  --bottomBar-color: #1AA6B7;
  --bottomBarAccent-color: #17BEBB;
}

#root {
  width: 100vw;
  height: 100vh;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.navBar {
    background-color: var(--topBar-color);
    position: relative;
    border-bottom: 5px solid var(--bottomBarAccent-color);
}

.navBarLeft {
    position: absolute;
    left: 20px;
    top: 40%;
    display: flex;
    align-items: center;
    justify-content: center;
}

.navBarLeft {
    color: white;
    font-size: 20px;
}

.navBarMid {
    font-family: 'Lato';
    font-weight: bold;
    letter-spacing: 1px;
    font-size: 0.9em;
    color: white;
    text-align: center;
}

.navBarRight {
    display: flex;
    align-items: center;
    justify-content: center;
}

.navBarHidden {
    grid-column-end: span 3;
    background-color: orange;
}

a {
    color: white;
    display: block;
  }
.homePage {
    display: grid;
    grid-gap: 1px;
}

.homeHeader {
    position: relative;
    height: 400px;
    border-radius: 10px;
}

.homeHeaderImg {
    height: 100%;
    background: -webkit-gradient(linear, left top, left bottom, color-stop(20%, rgba(0,0,0,0)), to(rgba(0,0,0,1))), url(/stemconf2019/static/media/header.93b0013f.jpg);
    background: -webkit-linear-gradient(top, rgba(0,0,0,0) 20%, rgba(0,0,0,1)), url(/stemconf2019/static/media/header.93b0013f.jpg);
    background: linear-gradient(to bottom, rgba(0,0,0,0) 20%, rgba(0,0,0,1)), url(/stemconf2019/static/media/header.93b0013f.jpg);
    background-position: center;
}

.homeHeaderText {
    color: white;
    font-weight: bolder;
    text-align: center;
    position: absolute;
    bottom: 0;
    top: 40%;
    left: 0;
    right: 0;
    margin: 0px;
    font-family: 'Lato'
}

.homeBox {
    background-color: #E8E8E8;
    font-family: 'Roboto';
    letter-spacing: 1.2px;
    line-height: 25px;
    padding: 5px;
    border-left: 5px solid var(--bottomBar-color);
}
.searchContainer {
    display: grid;
    grid-gap: 1px;
}

.findTernNest {
    position: relative;
    height: 400px;
    border-radius: 10px;
}

.findTernNestImg {
    height: 400px;
    background: -webkit-gradient(linear, left top, left bottom, color-stop(20%, rgba(0,0,0,0)), to(rgba(0,0,0,1))), url(/stemconf2019/static/media/search.cd629206.jpg);
    background: -webkit-linear-gradient(top, rgba(0,0,0,0) 20%, rgba(0,0,0,1)), url(/stemconf2019/static/media/search.cd629206.jpg);
    background: linear-gradient(to bottom, rgba(0,0,0,0) 20%, rgba(0,0,0,1)), url(/stemconf2019/static/media/search.cd629206.jpg);
    background-position: center;
    -webkit-filter: blur(1.2px);
            filter: blur(1.2px)
}

.findTernNestText {
    position: absolute;
    bottom: 0;
    margin-left: 20px;
    color: white;
    font-family: 'Lato', sans-serif;
    letter-spacing: 3px;
    font-size: 17px;
}

.viewActiveImg {
    height: 400px;
    background: -webkit-gradient(linear, left top, left bottom, color-stop(20%, rgba(0,0,0,0)), to(rgba(0,0,0,1))), url(/stemconf2019/static/media/search2.2969f9af.jpg);
    background: -webkit-linear-gradient(top, rgba(0,0,0,0) 20%, rgba(0,0,0,1)), url(/stemconf2019/static/media/search2.2969f9af.jpg);
    background: linear-gradient(to bottom, rgba(0,0,0,0) 20%, rgba(0,0,0,1)), url(/stemconf2019/static/media/search2.2969f9af.jpg);
    background-position: top;
    -webkit-filter: blur(1.2px);
            filter: blur(1.2px)
}

.findTernHeader {
    font-family: 'Lato';
    opacity: 0.6;
    display: flex;
    justify-content: space-between;
}

.findTernHeader h1 {
    margin: 10px;
    font-size: 1.4em;
}

.findTernHeader p {
    margin-right: 1em;
}

.ternContainer {

}

.ternGridContainer {
    display: grid;
    grid-gap: 1px;
}

.ternItems {
    background-color: white;
    display: grid;
    grid-template-columns: 70% 30%;
    padding: 10px;
}

.ternItems h1, .ternItems h2, .ternItems h3 {
    grid-column-start: 1;
    margin: 5px;
    font-family: 'Lato';
    font-weight: lighter;
}

.ternItems h2 {
    opacity: 0.8;
    font-size: 1.2em;
}

.ternItems h3 {
    opacity: 0.6;
    font-size: 1em;
}

.ternPic {
    background-color: red;
    background-image: url(/stemconf2019/static/media/search1.373df3c4.jpg);
    background-position: center;
    width: 100%;
    height: 100%;
    grid-row-start: 1;
    grid-row-end: span 3;
    grid-column-start: 2;
    
}
.reportContainer {
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-gap: 1px;
}

.reportButton {
    border-radius: 5px;
    position: relative;
    height: 400px;
}

.reportButton h1 {
    font-size: 1.5em;
    color: white;
    
}

.reportButtonImg {
    width: 100%;
    height: 100%;
}

.rpSubmit{
    background: -webkit-gradient(linear, left top, left bottom, color-stop(20%, rgba(0,0,0,0)), to(rgba(0,0,0,1))), url(/stemconf2019/static/media/pic1.f006de17.jpg);
    background: -webkit-linear-gradient(top, rgba(0,0,0,0) 20%, rgba(0,0,0,1)), url(/stemconf2019/static/media/pic1.f006de17.jpg);
    background: linear-gradient(to bottom, rgba(0,0,0,0) 20%, rgba(0,0,0,1)), url(/stemconf2019/static/media/pic1.f006de17.jpg);
    background-position: center;
}

.rpReport{
    background: -webkit-gradient(linear, left top, left bottom, color-stop(20%, rgba(0,0,0,0)), to(rgba(0,0,0,1))), url(/stemconf2019/static/media/pic2.17c8e361.jpg);
    background: -webkit-linear-gradient(top, rgba(0,0,0,0) 20%, rgba(0,0,0,1)), url(/stemconf2019/static/media/pic2.17c8e361.jpg);
    background: linear-gradient(to bottom, rgba(0,0,0,0) 20%, rgba(0,0,0,1)), url(/stemconf2019/static/media/pic2.17c8e361.jpg);
    background-position: center;
}

.rpLive{
    background: -webkit-gradient(linear, left top, left bottom, color-stop(20%, rgba(0,0,0,0)), to(rgba(0,0,0,1))), url(/stemconf2019/static/media/pic3.67d4dfc4.jpg);
    background: -webkit-linear-gradient(top, rgba(0,0,0,0) 20%, rgba(0,0,0,1)), url(/stemconf2019/static/media/pic3.67d4dfc4.jpg);
    background: linear-gradient(to bottom, rgba(0,0,0,0) 20%, rgba(0,0,0,1)), url(/stemconf2019/static/media/pic3.67d4dfc4.jpg);
    background-position: center;
    grid-column-end: span 2;
}

.reportButton h1 {
    position: absolute;
    bottom: 0;
    margin-left: 20px;
    color: white;
    font-family: 'Lato', sans-serif;
    letter-spacing: 3px;
    font-size: 17px;
}

.rpSubmitContainer h1 {
    text-align: center;
    font-family: 'Lato';
    color: #333;
    font-size: 1.3em;
}
form {
    padding: 10px;
}

input[type=text], select, textarea {
    width: 100%;
    padding: 12px;
    border: 1px solid #ccc;
    border-radius: 4px;
    box-sizing: border-box;
    margin-top: 6px;
    margin-bottom: 16px;
    resize: vertical;
  }
  
  input[type=submit] {
    float: right;
    background-color: var(--bottomBar-color);
    color: white;
    padding: 12px 20px;
    border: none;
    border-radius: 4px;
    cursor: pointer;
  }
  
  input[type=submit]:hover {
    background-color: #45a049;
  }

  
  label {
      font-family: 'Lato';
      opacity: 0.8;
  }
.forumContainer {

}

.topicGrid {
    display: grid;
    grid-template-columns: 80% 20%;
    grid-template-rows: 1fr;
    padding: 10px;
}

.topicsTitle {
    color: gray;
    font-family: 'Lato';
    font-size: 1.7em;
    font-weight: bolder;
    padding: 10px;
}

.postThread {
    display: flex;
    align-items: center;
    justify-content: center;
}
.postThread svg {
    background-color: var(--bottomBar-color);
    color: white;
    font-size: 1.5em;
    padding: 15px;
    border-radius: 50%;
}

.forumCards {
    margin-top: 15px;
    display: grid;
}


.forumCard {
    background-color: #F0F0F0;
    padding: 10px;
    height: 80px;
    display: grid;
    grid-template-rows: 4fr 1fr;
    grid-template-columns: 15% 85%;
    border-bottom: 1px solid rgba(0,0,0,0.2);
}

.forumUserCard {
    display: flex;
    align-items: center;
    justify-content: center;
    grid-row-end: span 2;
}

.forumUserCard svg {
    border-radius: 50%;
    background-color: var(--bottomBar-color);
    color: white;
    padding: 6px;
}

.forumCard h1 {
    margin: 5px;
    grid-column-start: 2;
    grid-column-end: span 3;
    font-size: 1em;
    font-family: 'Roboto';
    letter-spacing: 0.5px;
    font-weight: 500;
}

.forumCard h2 {
    margin: 5px;
    font-size: 0.8em;
    grid-column-start: 2;
    opacity: 0.6;
    font-weight: 400;
    font-family: 'Roboto';
}

.forumCard h3 {
    margin: 5px;
    font-size: 0.8em;
    grid-column-start: 3;
    grid-row-start: 2;
    opacity: 0.6;
    font-weight: 400;
}

.forumCard:first-child {
    border-radius: 10px 10px 0px 0px;
}

.forumCard:last-child {
    border-radius: 0px 0px 10px 10px;
}

.threadContainer {
    display: grid;
}

.threadForumCard {
    background-color: #F0F0F0;
    padding: 10px;
    height: 80px;
    display: grid;
    grid-template-rows: 4fr 1fr 1fr 1fr;
    grid-template-columns: 15% 85%;
}

.threadForumCard h1 {
    margin: 5px;
    grid-column-start: 2;
    grid-column-end: span 3;
    font-size: 1em;
    font-family: 'Roboto';
    letter-spacing: 0.5px;
    font-weight: 500;
}

.threadForumCard h2 {
    margin: 5px;
    font-size: 0.8em;
    grid-column-start: 2;
    opacity: 0.6;
    font-weight: 400;
    font-family: 'Roboto';
}

.threadForumCard h3 {
    grid-row-start: 3;
    grid-column-end: span 2;
    background-color: #F8F8F8;
    font-family: 'Roboto';
    font-size: 1.5em;
    opacity: 0.6;
    margin-left: 12px;
}

.threadComments {
    background-color: #F8F8F8;
}

.tcHeader {
    background-color: #F8F8F8;
    font-family: 'Roboto';
    font-size: 1.5em;
    padding-left: 12px;
}

.threadCommentItem {
    display: grid;
    grid-template-columns: 15% 85%;
    position: relative;
    padding: 10px;
}

.threadCommentItem h1 {
    margin: 5px;
    font-size: 1em;
    font-family: 'Roboto';
    letter-spacing: 0.5px;
    font-weight: 500;
}

.threadCommentItem h2 {
    margin: 5px;
    font-size: 1em;
    opacity: 0.6;
    font-weight: 400;
    font-family: 'Roboto';
}

.threadCommentItem h3{
    margin: 5px;
    font-size: 1em;
    opacity: 0.6;
    font-weight: 400;
    font-family: 'Roboto';
    position: absolute;
    right: 10px;
    top: 20%;
}

.addCommentContainer {
    background-color: #F8F8F8;
    display: grid;
    grid-template-columns: 80% 5% 15%;
    padding: 10px;
}

.addCommentWrap {
    background-color: #E8E8E8;
    border-radius: 15px;
}

.addCommentWrap h1{
    margin: 13px;
    opacity: 0.8;
    font-size: 1em;
    font-family: 'Roboto';
    letter-spacing: 0.5px;
    font-weight: 500;
}

.addCommentButton {
    display: flex;
    align-items: center;
    justify-content: center;
    grid-column-start: 3;
}

.addCommentButton svg {
    background-color: var(--bottomBar-color);
    color: white;
    padding: 10px;
    border-radius: 50%;
}
.mainContent {
    overflow: scroll;
    background-color: #E8E8E8;
}
.bottomBar {
    background-color: var(--bottomBar-color);
    border-top: 5px solid var(--bottomBarAccent-color);
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr 1fr; 
}

.bottomBarItem {
    border-right: 1px solid rgba(255,255,255,0.4);
    display: flex;
    align-items: center;
    justify-content: center;
}

.bottomBarItem:last-child{
    border-right: 0px solid transparent;
}

.bottomBarItem svg {
    font-size: 2em;
    color: white;
}
.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

.mainContainer {
  display: grid;
  grid-template-rows: 10% 80% 10%;
  width: 100%;
  height: 100%;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

